import React, { PropTypes } from 'react';
import { Link } from 'react-router';

import '../App.css';
import ClothesSelector from './ClothesSelector';
import LogoImg from '../assets/logo1.png';

import { Wrapper, Block, Header2, Title2, Logo, Button3, Center, ProgressBar, Progress, ProgressActive, ProgressDot, ProgressText } from './styledComponents';

const Step1 = ({ orderItems, addItem }) => (
  <Wrapper>
    <Block>
      <Header2>
        <Logo src={LogoImg} />
        <ProgressBar>
          <ProgressActive>
            <ProgressDot>
              1
            </ProgressDot>
            <ProgressText>
              Vetements
            </ProgressText>
          </ProgressActive>
          <Progress>
            <ProgressDot>
              2
            </ProgressDot>
            <ProgressText>
              Retouches
            </ProgressText>
          </Progress>
          <Progress>
            <ProgressDot>
              3
            </ProgressDot>
            <ProgressText>
              Mon RDV
            </ProgressText>
          </Progress>
        </ProgressBar>
        <Title2>
          Je choisis mes vêtements
        </Title2>
      </Header2>
      <ClothesSelector
        orderItems={orderItems}
        addItem={slug => addItem(slug)}
      />
      <Center>
        <Link to={'/order/step2'}><Button3>{'> Valider ces vêtements <'}</Button3></Link>
      </Center>
    </Block>
  </Wrapper>
);

Step1.propTypes = {
  orderItems: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  addItem: PropTypes.func.isRequired,
};

export default Step1;



